.menu-wrapper {
    margin-left: 315px;
}
.logistics-section-header {
    .bg-img-logistic {
        &::before {
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: rgba(15, 49, 58, 0.8);
            z-index: 0;
        }
    }
}
.logistics-home-section-1 {
    .bg-img-logistic {
        clip-path: polygon(0% 0%, 100% 0, 100% 82%, 95% 100%, 0% 100%);
        min-height: 580px;
    }
}
.logo-logistics {
    min-height: 200px;
    a {
        opacity: 0.4;
        transition: all 0.3s ease;
    }
    &:hover {
        a {
            opacity: 1;
        }
    }
}
.logistics-home-section-3 {
    clip-path: polygon(0% 0%, 100% 0, 100% 85%, 95% 100%, 0% 100%);
    min-height: 580px;
}
.logistics-services-section-2 {
    clip-path: polygon(0% 0%, 100% 0, 100% 90%, 95% 100%, 0% 100%);
    min-height: 580px;
}
.card-services {
    background-color: #163d47;
    clip-path: polygon(0% 0%, 100% 0, 100% 80%, 80% 100%, 0% 100%);
    transition: all 0.3s ease-in-out;
    border: 1px solid transparent;
    position: relative;
    .content {
        background-color: #163d47;
        clip-path: polygon(0% 0%, 100% 0, 100% 80%, 80% 100%, 0% 100%);
        z-index: 1000;
    }
    & .line-border {
        content: '';
        width: 102%;
        height: 102%;
        position: absolute;
        transition: all 0.3s ease-in-out;
        background-color: transparent;
        clip-path: polygon(0% 0%, 100% 0, 100% 80%, 80% 100%, 0% 100%);
        z-index: -100;
    }
    &:hover {
        .content-top {
            --bs-border-opacity: 1 !important;
        }
        background-color: var(--tc-theme-primary);
        .line-border {
            background-color: var(--tc-theme-primary);
        }
    }
    .content-bottom {
        transition: all 0.3s ease-in-out;
        p {
            opacity: 0.7;
        }
        a {
            &:hover {
                span {
                    text-decoration: underline;
                }
            }
        }
    }
}
.logistics-home-section-4 {
    .img-feature {
        clip-path: polygon(0% 0%, 100% 0, 100% 80%, 80% 100%, 0% 100%);
    }
}
.logistics-home-section-5 {
    .link-country {
        i {
            opacity: 0;
        }
        &:hover {
            p {
                color: var(--tc-system-navy-blue-2);
                transition: all 0.2s ease-in-out;
            }
            i {
                opacity: 1;
                transition: all 0.2s ease-in-out;
            }
        }
    }
    .box-bottom-search {
        align-items: flex-end;
        gap: 10px;
        .username-2 {
            background-image: url(../imgs/template/icons/user-w.svg);
        }
        .email-2 {
            background-image: url(../imgs/template/icons/email-white.svg);
        }
    }
    .dropdown-toggle::after {
        margin-left: 35px;
    }
    .dollar-search {
        &::before {
            content: '';
            display: inline-block;
            width: 16px;
            height: 16px;
            background: url(../imgs/pages/logistics/template/type-box.svg) no-repeat left center;
        }
    }
    .btn.show {
        color: var(--tc-system-white);
    }
    .btn {
        &:hover {
            color: var(--tc-system-white) !important;
        }
    }
    .form-control {
        color: var(--tc-system-white);
        max-width: 200px;
    }
}
.logistics-home-section-6 {
    .custom-container {
        clip-path: polygon(0% 0%, 100% 0, 100% 92%, 95% 100%, 0% 100%);
    }
    .card-project {
        clip-path: polygon(0% 0%, 100% 0, 100% 90%, 95% 100%, 0% 100%);
    }
}
.logistics-home-section-7 {
    margin-top: -200px;
    .img-bg {
        background-position: center;
        background-size: cover;
        min-height: 940px;
        clip-path: polygon(0% 0%, 100% 0, 100% 90%, 95% 100%, 0% 100%);
        position: relative;
    }
    .bg-overlay {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, #000 0%, rgba(0, 0, 0, 0) 100%);
    }
    svg {
        margin-top: -10px;

        text {
            font-family: 'Roboto', sans-serif;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: -0.8px;
            fill: transparent;
            stroke: #fff;
            stroke-width: 2px;
            animation: animated-stroke 2s;
            text-anchor: middle;
            dominant-baseline: middle;
        }
    }
}
.card-news-body {
    background-color: #fff;
    clip-path: polygon(0% 0%, 100% 0, 100% 80%, 90% 100%, 0% 100%);
    transition: all 0.3s ease-in-out;
    border: 1px solid transparent;
    position: relative;
    .content {
        background-color: white;
        clip-path: polygon(0% 0%, 100% 0, 100% 80%, 90% 100%, 0% 100%);

        z-index: 1000;
    }
    & .line-border {
        content: '';
        width: 102%;
        height: 102%;
        top: 0;
        left: 0;
        position: absolute;
        transition: all 0.3s ease-in-out;
        background-color: #e2e2e2;
        clip-path: polygon(0% 0%, 100% 0, 100% 80%, 90% 100%, 0% 100%);
        z-index: -100;
    }
    .content-bottom {
        transition: all 0.3s ease-in-out;
        p {
            opacity: 0.7;
        }
        a {
            &:hover {
                span {
                    text-decoration: underline;
                }
            }
        }
    }
}
.section-footer-20 {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: var(--Navy-Blue-2, #0f313a);
        opacity: 0.94;
    }
    .social-icons {
        .icon {
            transition: all 0.3s ease-in-out;
            svg {
                height: 14px;
                width: 14px;
                fill: var(--tc-system-white);
            }
            &:hover {
                background-color: var(--tc-theme-primary);
                border-color: var(--tc-theme-primary) !important;
            }
        }
    }
    .form-control {
        background-image: url(../imgs/template/icons/pen-w.svg);
        background-position: left 18px center;
        background-repeat: no-repeat;
        padding-left: 45px;
        color: var(--tc-system-white);
        &::placeholder {
            color: var(--tc-system-white);
            opacity: 0.3;
        }
    }
}
.card-team {
    .card-team-img {
        border-radius: 16px;
        background: #f3f3f3;
        min-height: 320px;
    }
    .social-group {
        transition: all 0.3s linear;
        opacity: 0;
        top: 100%;
        @media (max-width: 991px) {
            opacity: 1;
            top: 80%;
        }
        .icon {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            width: 40px;
            height: 40px;
            background: var(--tc-system-white);
            border-radius: 50%;
            transition: all 0.2s linear;
            svg {
                height: 20px;
                width: 20px;
                path {
                    fill: var(--tc-system-black);
                }
            }
            &:hover {
                background: var(--tc-theme-primary);
                svg {
                    path {
                        fill: var(--tc-system-white);
                        transition-duration: 0.2s;
                    }
                }
            }
        }
    }
    &:hover {
        .social-group {
            opacity: 1;
            top: 80%;
        }
    }
}
.steps-logistics {
    position: relative;
    cursor: pointer;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        border-bottom: 1px solid var(--tc-system-navy-blue-2);
        opacity: 0.1;
        bottom: 82px;
        z-index: -1;
    }
    @media (max-width: 1400px) {
        &::before {
            display: none;
        }
    }
    &:first-child {
        &::before {
            width: 50%;
        }
    }
    &:last-child {
        &::before {
            left: 0;
            width: 50%;
        }
    }
    &:hover {
        .icon-shape {
            transition: all 0.3s linear;
            background: var(--tc-theme-primary) !important;
        }
    }
}
.logistics-services-details-section-1 {
    .card {
        border-radius: 0px;
        border: 0;
        background-color: var(--tc-bg-light);
        transition: all 0.2s linear;
        overflow: hidden;
        .card-header {
            background: transparent;
            border-bottom: 0;
            a {
                .arrow {
                    &::before {
                        transform: rotate(0deg) !important;
                        font-family: 'remixicon';
                        content: '\ea4e';
                        transition: 0.3s;
                        color: var(--tc-system-black-2);
                        font-size: 24px;
                        font-weight: 300;
                    }
                }
                &.collapsed {
                    .arrow {
                        &::before {
                            content: '\ea4e';
                            transition: 0.3s;
                            transform: rotate(180deg) !important;
                        }
                    }
                }
            }
        }
    }
    & .active {
        background-color: var(--tc-system-navy-blue-2);
        .card-header {
            a {
                h6 {
                    color: var(--tc-system-white);
                }
                .arrow {
                    &::before {
                        color: var(--tc-system-white);
                    }
                }
            }
        }
    }
}
.card-case-study {
    .card-content {
        transition: all 0.3s linear;
        @media (min-width: 1400px) {
            opacity: 0;
        }
    }
    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: all 0.2s linear;
        background: rgba(15, 49, 58, 0.7);
    }
    &:hover {
        &::before {
            opacity: 1;
            transition: all 0.2s linear;
        }
        .card-content {
            opacity: 1;
            transition: all 0.3s linear;
        }
    }
}
.pagination_item {
    background: var(--tc-bg-light);
    color: var(--tc-system-black);
    border-radius: 8px !important;
    font-size: 20px !important;
    font-weight: 600 !important;
    svg {
        path {
            stroke: var(--tc-system-black) !important;
            transition: 0.3s ease-in-out;
        }
    }
    &.active {
        background: var(--tc-theme-primary);
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:hover {
        background: var(--tc-theme-primary);
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:focus {
        box-shadow: none;
        background-color: var(--tc-theme-primary) !important;
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
}
.blog-item {
    &:hover {
        background: var(--tc-theme-primary) !important;
        color: var(--tc-system-white) !important;
        transition: 0.2s linear;
    }
}
.logistics-contact-section-1 {
    .form-group {
        border-radius: 16px;
        border: 1px solid rgba(17, 17, 17, 0.1);
        background: var(--tc-bg-light);
        .form-control {
            color: var(--tc-system-black);
            border-radius: 8px;
            border: 1px solid rgba(17, 17, 17, 0.1);
            background: rgba(255, 255, 255, 0.05);
        }
        textarea {
            min-height: 170px;
        }
        .form-select {
            background-color: rgba(255, 255, 255, 0.05);
            border: 1px solid rgba(17, 17, 17, 0.1);
            border-radius: 8px;
            &:focus {
                outline: none;
                box-shadow: none;
            }
        }
    }
}
